import { sort } from './sort';
export function getComputedOcpReportItems({ report, idKey, labelKey = idKey, sortKey = 'charge', sortDirection = 0 /* asc */, }) {
    return sort(getUnsortedComputedOcpReportItems({
        report,
        idKey,
        labelKey,
        sortDirection,
        sortKey,
    }), {
        key: sortKey,
        direction: sortDirection,
    });
}
export function getUnsortedComputedOcpReportItems({ report, idKey, labelKey = idKey, }) {
    if (!report) {
        return [];
    }
    const itemMap = {};
    const visitDataPoint = (dataPoint) => {
        if (dataPoint.values) {
            dataPoint.values.forEach(value => {
                const capacity = value.capacity;
                const charge = value.charge;
                const id = value[idKey];
                let label = value[labelKey];
                if (labelKey === 'cluster' && value.cluster_alias) {
                    label = value.cluster_alias;
                }
                const limit = value.limit;
                const request = value.request;
                const usage = value.usage;
                if (!itemMap[id]) {
                    itemMap[id] = {
                        app: value.app,
                        capacity,
                        charge,
                        deltaPercent: value.delta_percent,
                        deltaValue: value.delta_value,
                        id,
                        label,
                        limit,
                        request,
                        units: value.units || usage ? 'GB' : 'USD',
                        usage,
                    };
                    return;
                }
                itemMap[id] = Object.assign({}, itemMap[id], { capacity: itemMap[id].capacity + capacity, charge: itemMap[id].charge + charge, limit: itemMap[id].limit + limit, request: itemMap[id].request + request, usage: itemMap[id].usage + usage });
            });
        }
        for (const key in dataPoint) {
            if (dataPoint[key] instanceof Array) {
                return dataPoint[key].forEach(visitDataPoint);
            }
        }
    };
    if (report && report.data) {
        report.data.forEach(visitDataPoint);
    }
    return Object.values(itemMap);
}
export function getIdKeyForGroupBy(groupBy = {}) {
    if (groupBy.project) {
        return 'project';
    }
    if (groupBy.cluster) {
        return 'cluster';
    }
    if (groupBy.node) {
        return 'node';
    }
    return 'date';
}
