export const unitLookupKey = unit => unit ? unit.split('-')[0].toLowerCase() : '';
export const formatValue = (value, unit, options = {}) => {
    const lookup = unitLookupKey(unit);
    const fValue = value || 0;
    switch (lookup) {
        case 'usd':
            return formatCurrency(fValue, lookup, options);
        case 'gb':
            return formatStorage(fValue, lookup, options);
        default:
            return unknownTypeFormatter(fValue, lookup, options);
    }
};
const unknownTypeFormatter = (value, _unit, { fractionDigits } = {}) => {
    return value.toFixed(fractionDigits);
};
export const formatCurrency = (value, _unit, { fractionDigits = 2 } = {}) => {
    let fValue = value;
    if (!value) {
        fValue = 0;
    }
    return fValue.toLocaleString('en', {
        style: 'currency',
        currency: 'USD',
        minimumFractionDigits: fractionDigits,
        maximumFractionDigits: fractionDigits,
    });
};
export const formatStorage = (value, _unit, { fractionDigits = 2 } = {}) => {
    return value.toFixed(fractionDigits);
};
