import endOfMonth from 'date-fns/end_of_month';
import format from 'date-fns/format';
import getDate from 'date-fns/get_date';
import startOfMonth from 'date-fns/start_of_month';
import i18next from 'i18next';
import { unitLookupKey, } from 'utils/formatValue';
import { getComputedAwsReportItems, } from 'utils/getComputedAwsReportItems';
import { getComputedOcpReportItems, } from 'utils/getComputedOcpReportItems';
export function transformAwsReport(report, type = 1 /* daily */, key = 'date') {
    if (!report) {
        return [];
    }
    const items = {
        report,
        idKey: key,
        sortKey: 'id',
        sortDirection: 1 /* desc */,
    };
    const computedItems = getComputedAwsReportItems(items);
    if (type === 1 /* daily */) {
        return computedItems.map(i => createDatum(i.total, i, key));
    }
    if (type === 2 /* monthly */) {
        return computedItems.map(i => createDatum(i.total, i, key));
    }
    return computedItems.reduce((acc, d) => {
        const prevValue = acc.length ? acc[acc.length - 1].y : 0;
        return [...acc, createDatum(prevValue + d.total, d, key)];
    }, []);
}
export function transformOcpReport(report, type = 1 /* daily */, key = 'date', reportItem = 'charge') {
    if (!report) {
        return [];
    }
    const items = {
        report,
        idKey: key,
        sortKey: 'id',
        sortDirection: 1 /* desc */,
    };
    const computedItems = getComputedOcpReportItems(items);
    if (type === 1 /* daily */) {
        return computedItems.map(i => createDatum(i[reportItem], i, key));
    }
    if (type === 2 /* monthly */) {
        return computedItems.map(i => createDatum(i[reportItem], i, key));
    }
    return computedItems.reduce((acc, d) => {
        const prevValue = acc.length ? acc[acc.length - 1].y : 0;
        return [...acc, createDatum(prevValue + d[reportItem], d, key)];
    }, []);
}
export function createDatum(value, computedItem, idKey = 'date') {
    const xVal = idKey === 'date' ? getDate(computedItem.id) : computedItem.label;
    const yVal = isFloat(value)
        ? parseFloat(value.toFixed(2))
        : isInt(value)
            ? value
            : 0;
    return {
        x: xVal,
        y: yVal,
        key: computedItem.id,
        name: computedItem.id,
        units: computedItem.units,
    };
}
export function getDatumDateRange(datums) {
    if (!(datums && datums.length)) {
        const today = new Date();
        const firstOfMonth = startOfMonth(today);
        return [firstOfMonth, today];
    }
    const start = new Date(datums[0].key + 'T00:00:00');
    const end = new Date(datums[datums.length - 1].key + 'T00:00:00');
    return [start, end];
}
export function getDateRangeString(datums, firstOfMonth = true, lastOfMonth = false) {
    const [start, end] = getDatumDateRange(datums);
    // Show the date range we are trying to cover (i.e., days 1-30/31)
    if (firstOfMonth && start.setDate) {
        start.setDate(1);
    }
    if (lastOfMonth && start.setDate) {
        const lastDate = endOfMonth(start).getDate();
        end.setDate(lastDate);
    }
    const monthName = format(start, 'MMM');
    const startDate = getDate(start);
    const endDate = getDate(end);
    return `${monthName} ${getDate(start)}${startDate !== endDate ? ` - ${endDate}` : ''}`;
}
export function getTooltipContent(formatValue) {
    return function labelFormatter(value, unit = null, options = {}) {
        const lookup = unitLookupKey(unit);
        switch (lookup) {
            case 'hrs':
            case 'gb':
                return i18next.t(`units.${lookup}`, {
                    value: `${formatValue(value, unit, options)}`,
                });
            default:
                return `${formatValue(value, unit, options)}`;
        }
    };
}
export function getTooltipLabel(datum, formatValue, formatOptions, idKey = 'date') {
    if (!datum.key) {
        return '';
    }
    if (idKey === 'date') {
        const date = format(datum.key, 'MMM D YYYY');
        return `${date}: ${formatValue(datum.y, datum.units, formatOptions)}`;
    }
    return datum.key.toString();
}
function isInt(n) {
    return Number(n) === n && n % 1 === 0;
}
function isFloat(n) {
    return Number(n) === n && n % 1 !== 0;
}
